@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{height:80px;width:100%;display:flex;padding:0 10%}.nav .logo{width:10rem}nav ul{padding:0}nav ul li{display:inline-block;line-height:80px;list-style:none}nav ul li a{color:var(--main-font-color);font-size:17px;padding:7px 13px;border-radius:3px;font-weight:normal}.checkbtn{font-size:30px;color:#212121;float:left;line-height:80px;cursor:pointer;display:none}nav input{display:none}.fa-times{display:none !important}.contact-container{display:flex;flex-wrap:nowrap;align-items:center;justify-content:flex-end;width:100%}.contact{padding:15px 20px;color:red;background-color:white;border-radius:3px;border:none;box-shadow:3px 0px 10px 0px rgba(0,0,0,0.2);font-size:13px;padding-right:35px}.contact:hover i{opacity:0;padding-left:20px}.contact i{top:34px;font-weight:20px;font-size:15px;position:absolute;padding-left:10px;transition:1s ease-out}.menu{display:flex;width:100%;height:100%;justify-content:space-evenly;align-items:center;position:relative;left:0}@media (max-width: 858px){.checkbtn{display:block;max-width:20px !important}nav{position:fixed;z-index:1500;background-color:white;padding:0 !important}.nav::before{content:"";position:fixed;width:100%;height:calc(100vh - 80px);bottom:0;background-color:rgba(0,0,0,0.8);opacity:0;-webkit-transform:scale(0);transform:scale(0);transition:opacity 0.5s}.open .fa-times{display:inline-block !important}.open .fa-bars{display:none}.open .nav::before{opacity:1;-webkit-transform:scale(1);transform:scale(1);border-bottom:1px solid var(--main-font-color)}.open nav ul{left:0}.nav ul{position:fixed;width:80%;height:100vh;background:white;top:80px;left:-100%;text-align:center;transition:0.5s;z-index:1200;margin:0}nav ul li{display:block;margin:50px 0;line-height:30px}nav ul li a{font-size:20px}.menu{justify-content:space-around}.contact{padding:10px 15px;width:130px;font-size:12px}.contact i{display:none}}

.modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.7);z-index:1500}.modal .modal-main{position:fixed;background:white;width:60%;height:auto;padding:30px;top:50%;left:50%;border-radius:20px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.modal .modal-main .fa-times{display:block !important;position:absolute;right:40px;top:60px;font-size:20px;cursor:pointer}@media (max-width: 858px){.modal .modal-main{width:80%}}.modal .modal-main .name-email{flex-direction:column}.modal .modal-main .name-email .group{margin:0 !important}.modal .modal-main .name-email .group:nth-child(1) .form-input{margin-bottom:5px}.modal .modal-main .name-email .form-input{margin:25px 0}.display-block{display:block}.display-none{display:none}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:#F4FAFE;border-radius:5px;color:#314584;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-16px;font-size:15px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:#314584;font-size:20px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-16px;font-size:15px;color:#000}

.button{padding:10px 20px;background-color:red;color:white;border:none;border-radius:25px;box-shadow:0px 5px 10px 0px rgba(255,57,70,0.15);transition:1s;font-size:15px;font-weight:600}.button:hover{padding:12px 25px}

.form{width:100%}.form h3{font-size:46px;margin:10px 0;color:#314584}.form .group:nth-child(3){margin:10px 0}.form .group:nth-child(3) .form-input{margin:0px;height:100px}.form .name-email{width:100%;display:flex;justify-content:center;align-items:center}.form .name-email .group{width:100%;display:block;position:relative;bottom:0;height:100%;margin:0}.form .name-email .group:nth-child(1){margin-right:2%}.form .name-email .group .form-input:focus ~ .form-input-label{top:4px;font-size:15px;color:black}.form .name-email .group .form-input-label{top:35px}.form .name-email .group .form-input-label.shrink{top:4px;font-size:15px;color:black}.form .button{display:block;margin:30px auto}@media (max-width: 500px){.name-email{flex-direction:column}.name-email .form-input{margin:25px 0}.group:nth-child(3){margin:25px 0 !important}}

.services-container{width:100%;display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap}.services-container .items{display:flex;flex-wrap:wrap}@media (max-width: 1100px){.services-container .items{width:100%;justify-content:space-around;margin:0}}.services-container .action{width:33%}@media (max-width: 1100px){.services-container .action{width:100%;text-align:center;margin:3em 0}}.services-container .action h2{font-size:45px}

.item-container{display:flex;flex-direction:column;justify-content:space-evenly;align-items:flex-start;width:260px;height:360px;margin:10px 15px;padding:30px;box-shadow:0px 5px 10px 0px rgba(25,45,51,0.15);word-wrap:break-word}.item-container .image{width:60px}.item-container p{width:100%}.item-container h3{font-size:27px;color:#314584}

.intro-container{display:flex;flex-direction:row}.intro-text{width:49%}.intro-text h1{color:#314584;font-size:2.5em}.intro-text p{font-size:.9em;font-weight:300;line-height:1.7em;margin-bottom:2em}.intro-image{width:49%;margin-left:2em}.intro-image .shape{width:100%;height:300px;position:relative;margin-right:0}.unstyled-button{padding:10px 20px;background-color:white;color:#314584;border:none;border-radius:25px;box-shadow:0px 5px 10px 0px rgba(255,57,70,0.15);font-size:1em;margin:0 1em;font-weight:600;border:1px solid #fff}.unstyled-button:hover{border:1px solid #314584}@media (max-width: 858px){.intro-container{padding-top:6em}}@media (max-width: 830px){.intro-container{flex-direction:column;text-align:center}.intro-text{width:100%}.intro-image{margin:2em auto}}@media (max-width: 375px){.unstyled-button{margin-top:1em}}

.about{display:flex;flex-direction:row;margin:4em 0;width:100%}.about .about-image{width:49%}.about .about-image .ab-img{width:95%;height:300px}.about .about-text{width:49%}.about .about-text span{color:red;font-size:.9em}.about .about-text h1{color:#314584;font-weight:600}.about .about-text p{font-size:.9em;line-height:1.5em}@media (max-width: 858px){.about{flex-direction:column;text-align:center}.about .about-image{display:none}.about .about-text{width:100%;margin:auto;text-align:center}}

.portofolio-container{width:100%;color:#314584;display:flex;flex-direction:column}.portofolio-container span{text-align:center;color:red;cursor:pointer;font-weight:500}.portofolio-container .portofolio-text h1{font-weight:600;margin:0}.portofolio-container .portofolio-images{margin:auto}.portofolio-container .portofolio-images .portofolio-row{display:flex}.portofolio-container .portofolio-images .portofolio-row img{width:200px;height:200px;margin:1em 1em;cursor:pointer}.portofolio-container .portofolio-images .portofolio-row img:hover{opacity:.5}@media (max-width: 858px){.portofolio-container .portofolio-images{margin:0}.portofolio-container .portofolio-images .portofolio-row{display:flex;flex-direction:column;align-items:center}.portofolio-container .portofolio-images .portofolio-row img{width:350px;background:blue}}

.news-container{margin:2em 0;display:flex;flex-direction:column}.news-container .news-text span{color:red;font-weight:500}.news-container .news-text h1{color:#314584;font-weight:600}.news-container .news-cards{display:flex;flex-direction:row}.news-container .news-cards .card{color:#314584;box-shadow:0px 5px 10px 0px rgba(25,45,51,0.15);width:250px;height:200px;margin:auto;cursor:pointer}.news-container .news-cards .card:hover{background:#FF3E4D;color:white}.news-container .news-cards .card .card-content{margin:1em 2em}.news-container .news-cards .card .card-content p{line-height:1.3em}@media (max-width: 858px){.news-container .news-cards{display:flex;flex-direction:column}.news-container .news-cards .card{margin:1em auto;width:300px}}

.container{padding:0 10%}

*{
  text-decoration: none;
  box-sizing: border-box;
}
body{
  font-family:'Work Sans', sans-serif;;
}
.App{
  max-width: 1800px;
  margin: 0 auto;
}
