.services-container{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;


    .items{
        display: flex;
        flex-wrap: wrap;
        @media (max-width: 1100px) {
            width: 100%;
            justify-content: space-around;
            margin: 0;
    
        }
    }

    

    .action{

        width: 33%;
        @media (max-width: 1100px) {
            width: 100%;
            text-align: center;
            margin: 3em 0;
    
        }
        


        h2{
            font-size: 45px;
            
        }
    }
    
}
